import React, { useState, useEffect } from "react";
import DashboardIcon from "@material-ui/icons/Dashboard";
import PeopleIcon from "@material-ui/icons/People";
import AssignmentIcon from "@material-ui/icons/Assignment";
import CreateIcon from "@material-ui/icons/Create";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import { Link } from "react-router-dom";
import Section from "../server/services/classes";
import {
  ListItem,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  List,
  Divider,
} from "@material-ui/core";

const ListItems = ({ match }) => {
  const [sections, setSections] = useState([]);

  useEffect(() => {
    Section.getProfClasses(localStorage.getItem("email"))
      .then(returnedData => {
        setSections(returnedData);
      })
      .catch(error => {
        console.log("error :>> ", error);
      });
  }, []);

  return (
    <>
      <Divider />
      <List>
        <div>
          <ListSubheader inset>Student Management</ListSubheader>
          <Link to={`${match.url}`} style={{ all: "inherit" }}>
            <ListItem button>
              <ListItemIcon>
                <DashboardIcon />
              </ListItemIcon>
              <ListItemText primary="Dashboard" />
            </ListItem>
          </Link>
          <Link to={`${match.url}/exams`} style={{ all: "inherit" }}>
            <ListItem button>
              <ListItemIcon>
                <CreateIcon />
              </ListItemIcon>
              <ListItemText primary="Exams" />
            </ListItem>
          </Link>

          <Link to={`${match.url}/students`} style={{ all: "inherit" }}>
            <ListItem button>
              <ListItemIcon>
                <PeopleIcon />
              </ListItemIcon>
              <ListItemText primary="Classes" />
            </ListItem>
          </Link>
        </div>
      </List>

      <Divider />

      <List>
        <div>
          <ListSubheader inset>Class reports</ListSubheader>
          {sections.map((section, i) => {
            return (
              <ListItem button key={i}>
                <ListItemIcon>
                  <AssignmentIcon />
                </ListItemIcon>
                <ListItemText primary={section.label} />
              </ListItem>
            );
          })}
        </div>
      </List>

      <Divider />

      <List>
        <div>
          <div>
            <Link
              to="/"
              style={{ all: "inherit" }}
              onClick={() => {
                localStorage.clear();
                window.location.replace("/");
              }}>
              <ListItem button>
                <ListItemIcon>
                  <ExitToAppIcon />
                </ListItemIcon>
                <ListItemText primary="Log Out" />
              </ListItem>
            </Link>
          </div>
        </div>
      </List>
    </>
  );
};

export default ListItems;
